/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { Component } from "react";
import { Icon, Popconfirm, Divider, Modal, Input, Button, Select } from "antd";
import { AntTable } from "../../styledcomponents/table/AntTabl";
import { api } from "../../services/AxiosService";
import Notification from "../../Constant/Notification";
import { connect } from "react-redux";
import { SWITCH_TO_EDIT_MODE } from "../../../redux/action/master/plantlevel/PlantLevel";
import AddSieveSize from "./title/AddSieveSize";
import { PrimaryButton } from "../../styledcomponents/button/button";
import { MasterLevelForm } from "../../styledcomponents/form/MasterLevelForms";
import { Main_Category_Name_Aggregate } from "../../Constant/Api";
import Notificationfuc from "../../Constant/Notification";

const { Option } = Select;

class SieveSize extends Component {
  state = {
    searchText: "",
    visible: false,
    size: "small",
    loading: false,
    visible1: false,
    data: "",
    id: "",
    sizeId: "",
    sub_category: "",
    Aggsize: "",
    minValue: "",
    maxValue: "",
    edit_Aggsize: "",
    edit_sub_category: ""
  };

  componentWillMount() {
    if (window.screen.width > 1900) {
      console.log("hooray");
      this.setState({
        size: "large"
      });
    } else if (window.screen.width < 1440) {
      this.setState({
        size: "small"
      });
    }
  }
  componentDidMount() {
    this.getallPlant();
    this.getSubCategoryByMain();
  }

  //  get all plant API
  getallPlant = () => {
    let datalist = [];
    api("GET", "supermix", "/sieve-accepted-values", "", "", "").then(res => {
      console.log(res.data);
      let x = res.data.results.sieveAcceptedValues;
      for (var i = 0; i < x.length; i++) {
        datalist.push({
          id: x[i].id,
          size: x[i].sieveSize.size,
          min: x[i].min,
          max: x[i].max,
          materialSubCategoryName: x[i].sieveSize.materialSubCategoryName,
          materialSubCategoryId: x[i].sieveSize.materialSubCategoryId,
          sizeId: x[i].sieveSize.id
        });
      }
      this.setState({
        datalist
      });
    });
  };

  showModal = () => {
    this.setState({
      visible: true
    });
  };

  handleOk = e => {
    console.log(e);
    this.setState({
      visible: false
    });
  };

  handleCancel = e => {
    console.log(e);
    this.setState({
      visible: false
    });
  };

  handleChange = (event, field) => {
    this.setState({ [field]: event.target.value });

    event.preventDefault();
    const { name, value } = event.target;
    console.log(name + " is \n" + value);
    this.setState({ [name]: value });
  };

  clearFilters = () => {
    this.setState({ filteredInfo: null });
  };

  clearAll = () => {
    this.setState({
      filteredInfo: null,
      sortedInfo: null
    });
  };

  setAgeSort = () => {
    this.setState({
      sortedInfo: {
        order: "descend",
        columnKey: "age"
      }
    });
  };

  onChange(pageNumber) {
    console.log("Page: ", pageNumber);
  }

  // plant delete API

  onConfirmdelete(code) {
    console.log(code);
    api("DELETE", "supermix", "/sieve-accepted-value", "", "", code).then(
      res => {
        console.log(res.data);
        this.getallPlant();
        Notification("success", res.data.message);
      }
    );
    console.log(this.state.id);
  }
  showModal1 = record => {
    console.log(record);
    this.setState({
      visible1: true,
      sizeId: record.id,
      Aggsize: record.sizeId,
      edit_Aggsize: record.size,
      sub_category: record.materialSubCategoryId,
      edit_sub_category: record.materialSubCategoryName,
      minValue: record.min,
      maxValue: record.max
    });
  };
  handleCancel1 = () => {
    this.setState({
      visible1: false,
      sizeId: "",
      sub_category: "",
      Aggsize: "",
      minValue: "",
      maxValue: "",
      edit_Aggsize: "",
      edit_sub_category: ""
    });
  };

  handleSelect = (name, value) => {
    console.log(name);
    console.log(value);

    if (name === "sub_category") {
      this.setState({
        sub_category: value,
        edit_sub_category: value
      });
      this.getAllSizeBySubId(value);
    }
    if (name === "Aggsize") {
      this.setState({
        Aggsize: value,
        edit_Aggsize: value
      });
    }
  };

  getAllSizeBySubId = id => {
    api(
      "GET",
      "supermix",
      "/sieve-size/material-sub-category/",
      "",
      "",
      id
    ).then(res => {
      console.log(res.data.results.materialSubCategoryId);
      let x = res.data.results.materialSubCategoryId;
      let selectSize = x.map((post, index) => {
        return (
          <Option value={post.id} key={index}>
            {post.size}
          </Option>
        );
      });
      this.setState({
        selectSize
      });
    });
  };
  getSubCategoryByMain = () => {
    let optData = [];
    api(
      "GET",
      "supermix",
      "/material-sub-category/materialCategoryName",
      "",
      "",
      Main_Category_Name_Aggregate
    ).then(res => {
      let dataSub = res.data.results.materialSubCategories;
      console.log(dataSub);
      let selectCategory = dataSub.map((post, index) => {
        return (
          <Option value={post.id} key={index}>
            {post.name}
          </Option>
        );
      });

      this.setState({
        selectCategory
      });
    });
  };
  handleSubmit = e => {
    let eM = "can't be empty";
    e.preventDefault();

    const data = {
      id: this.state.sizeId,
      sieveSizeId: this.state.Aggsize,
      materialSubCategoryId: this.state.sub_category,
      min: this.state.minValue,
      max: this.state.maxValue
    };
    console.log(data);

    api("PUT", "supermix", "/sieve-accepted-value", "", data, "").then(
      res => {
        console.log(res.data);

        Notificationfuc("success", res.data.message);
        this.getallPlant();
        this.setState({
          visible1: false,
          sizeId: "",
          sub_category: "",
          Aggsize: "",
          minValue: "",
          maxValue: "",
          edit_Aggsize: "",
          edit_sub_category: ""
        });
        setTimeout(() => {}, 500);
      },
      error => {
        this.setState({
          // errormgs: error.validationFailures[0]
        });
        console.log("DEBUG34: ", error);
        // console.log(HandelError(error.validationFailures[0]));
      }
    );
  };
  render() {
    const { visible1, loading } = this.state;
    const columns = [
      {
        title: "Size (mm)",
        dataIndex: "size",
        key: "id",
        width: "5%"
      },
      {
        title: "Minimum Value",
        dataIndex: "min",
        key: "unit",
        width: "7%"
      },
      {
        title: "Maximum Value",
        dataIndex: "max",
        key: "action",
        width: "7%"
      },
      {
        title: "SubCategory",
        dataIndex: "materialSubCategoryName",
        key: "action",
        width: "7%"
      },
      {
        title: "Edit & Delete",
        key: "action",
        width: "7%",
        render: (text, record = this.state.datalist) => (
          <span>
            <a>
              <Icon
                type="edit"
                style={{ fontSize: "1.2em" }}
                onClick={this.showModal1.bind(this, record)}
              />
            </a>
            <Divider type="vertical" />
            <a>
              <Popconfirm
                title="Are you sure you want to Delete this?"
                icon={
                  <Icon type="question-circle-o" style={{ color: "red" }} />
                }
                onConfirm={this.onConfirmdelete.bind(this, record.id)}
              >
                <a href="#">
                  <Icon
                    type="delete"
                    style={{ color: "red", fontSize: "1.2em" }}
                  />
                </a>
              </Popconfirm>
            </a>
          </span>
        )
      }
    ];

    return (
      <div>
        <AntTable
          length
          title={() => <AddSieveSize reload={this.getallPlant} />}
          className="plantManageTable"
          columns={columns}
          dataSource={this.state.datalist}
          onChange={this.handleChange}
          pagination={{ defaultPageSize: 7 }}
          size={this.state.size}
        />
        <div>
          <Modal
            visible={visible1}
            title="Edit Sieve AcceptedValue"
            closable={false}
            loading={loading}
            onCancel={this.handleCancel1}
            footer={[
              <Button key="back" onClick={this.handleCancel1}>
                Cancel
              </Button>,
              <PrimaryButton
                key="submit"
                loading={loading}
                onClick={this.handleSubmit}
                style={{
                  background: "#001328",
                  color: "white",
                  border: "none"
                }}
              >
                Edit
              </PrimaryButton>
            ]}
            width="600px"
          >
            <MasterLevelForm style={{ justifyContent: "space-evenly" }}>
              <div className="input_wrapper">
                <label for="sub_category" className="label">
                  Material Sub Category:
                </label>
                <Select
                  id="sub_category"
                  name="sub_category"
                  placeholder="Select category"
                  style={{ width: 120 }}
                  value={this.state.edit_sub_category}
                  onChange={value => this.handleSelect("sub_category", value)}
                >
                  {this.state.selectCategory}
                </Select>
                <div style={{ height: "12px" }}></div>
              </div>
              <div className="input_wrapper">
                <label for="plant_code" className="label">
                  Size:
                </label>
                <Select
                  id="Aggsize"
                  name="Aggsize"
                  placeholder="Select size"
                  style={{ width: 120 }}
                  value={this.state.edit_Aggsize}
                  onChange={value => this.handleSelect("Aggsize", value)}
                >
                  {this.state.selectSize}
                </Select>

                <div style={{ height: "12px" }}></div>
              </div>
              <div className="input_wrapper">
                <label for="plant_code" className="label">
                  Minimum Value:
                </label>
                <Input
                  id="minValue"
                  name="minValue"
                  placeholder="Enter the minValue "
                  value={this.state.minValue}
                  onChange={this.handleChange}
                />
                <div style={{ height: "12px" }}></div>
              </div>
              <div className="input_wrapper">
                <label for="plant_code" className="label">
                  Maximum Value:
                </label>
                <Input
                  id="maxValue"
                  name="maxValue"
                  placeholder="Enter the maxValue "
                  value={this.state.maxValue}
                  onChange={this.handleChange}
                />
                <div style={{ height: "12px" }}></div>
              </div>
            </MasterLevelForm>
          </Modal>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => null;

const mapDispatchToProps = dispatch => {
  return {
    // if this function dispatches modal will be shown and the data will be drawn :)
    passEditPlantRecordtoModal: record => {
      //this payload is the data we pass into redux which is in the row which we clicked
      dispatch({ type: SWITCH_TO_EDIT_MODE, payload: record });
      console.log(record);
    }
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(SieveSize);
