import React, { Component } from "react";
import {
  Input,
  Modal,
  Button,
  Icon,
  Select,
  Switch,
  Checkbox,
  Popconfirm,
  Divider
} from "antd";
import AcceptedValueTitle from "../ManageTables/titles/AcceptedValueTitle";
import AdmixtureValueTitle from "../ManageTables/titles/AdmixtureValueTitle";
import { FlexContainer } from "../../../../styledcomponents/container/FlexGrid";
import { PrimaryButton } from "../../../../styledcomponents/button/button";
import {
  MasterLevelForm,
  MasterLevelFormTitle
} from "../../../../styledcomponents/form/MasterLevelForms";
import { AntTable } from "../../../../styledcomponents/table/AntTabl";
import { api } from "../../../../services/AxiosService";
import Notification from "../../../../Constant/Notification";

const { Option } = Select;

const error = {
  color: "red",
  fontSize: "12px",
  width: "170px",
  height: "0.2px"
};

const minierror = {
  width: "90px",
  color: "red",
  fontSize: "12px",
  height: "0.2px"
};

class AcceptedValue extends Component {
  state = {
    loading: false,
    visible: false,

    searchText: "",
    visible: false,
    size: "small",

    admixturedata: "",
    acceptedvaluedata: "",
    id: "",
    switchVal: false,
    test_name: undefined,
    parameter: undefined,
    unit: undefined,
    maximum: "",
    minimum: "",
    subCategory: undefined,
    material: undefined,
    switchValMaximum: "",
    switchValMinimum: "",

    errors: {
      parameter: "",
      unit: "",
      maximum: "",
      minimum: ""
    },
    optErrors: {
      subCategory: "",
      material: "",
      switchValMaximum: "",
      switchValMinimum: ""
    },
    type: "edit",
    accId: "",
    errormsgs: "",
    switchType: ""
  };

  handleSelect = (name, value) => {
    const { errors, optErrors } = this.state;
    console.log(value + " of " + name);

    //test
    if (name === "test_name") {
      this.setState({
        test_name: value
      });
    }

    //parameter
    if (name === "parameter") {
      this.setState({
        parameter: value,
        errors: {
          parameter: "",
          unit: errors.unit,
          maximum: errors.maximum,
          minimum: errors.minimum
        },
        optErrors: {
          subCategory: optErrors.subCategory,
          material: optErrors.material,
          switchValMaximum: optErrors.switchValMaximum,
          switchValMinimum: optErrors.switchValMinimum
        }
      });
    }

    //unit
    if (name === "unit") {
      this.setState({
        unit: value,
        errors: {
          parameter: errors.parameter,
          unit: "",
          maximum: errors.maximum,
          minimum: errors.minimum
        },
        optErrors: {
          subCategory: optErrors.subCategory,
          material: optErrors.material,
          switchValMaximum: optErrors.switchValMaximum,
          switchValMinimum: optErrors.switchValMinimum
        }
      });
    }

    //subCategory
    if (name === "subCategory") {
      this.setState({
        subCategory: value,
        errors: {
          parameter: errors.parameter,
          unit: errors.unit,
          maximum: errors.maximum,
          minimum: errors.minimum
        },
        optErrors: {
          subCategory: "",
          material: optErrors.material,
          switchValMaximum: optErrors.switchValMaximum,
          switchValMinimum: optErrors.switchValMinimum
        }
      });
    }

    //material
    if (name === "material") {
      this.setState({
        material: value,
        errors: {
          parameter: errors.parameter,
          unit: errors.unit,
          maximum: errors.maximum,
          minimum: errors.minimum
        },
        optErrors: {
          subCategory: optErrors.subCategory,
          material: "",
          switchValMaximum: optErrors.switchValMaximum,
          switchValMinimum: optErrors.switchValMinimum
        }
      });
    }
  };

  onChange(checked) {
    console.log(`switch to ${checked}`);
    if (checked === true) {
      console.log("true");
      this.setState({
        switchVal: true
      });
    }
    if (checked === false) {
      console.log("false");
      this.setState({
        switchVal: false
      });
    }
  }

  getAllParameters() {
    api("GET", "supermix", "/parameters", "", "", "").then(res => {
      console.log(res.data.results);
      if (res.data.results.parameters.length > 0) {
        console.log("got parameters");
        let SelectParameter = res.data.results.parameters.map((post, index) => {
          return (
            <Option value={post.id} key={index}>
              {post.name}
            </Option>
          );
        });
        this.setState({
          SelectParameter
        });
      }
    });
  }

  //get all for unit select
  getAllUnits() {
    api("GET", "supermix", "/units", "", "", "").then(res => {
      console.log(res.data.results);
      if (res.data.results.units.length > 0) {
        console.log("got units");
        let SelectUnit = res.data.results.units.map((post, index) => {
          return (
            <Option value={post.id} key={index}>
              {post.unit}
            </Option>
          );
        });
        this.setState({
          SelectUnit
        });
      }
    });
  }

  //get all for materials select
  getAllMaterials() {
    api("GET", "supermix", "/raw-materials", "", "", "").then(res => {
      console.log(res.data.results);
      if (res.data.results.rawMaterial.length > 0) {
        console.log("got materials");
        let SelectMaterial = res.data.results.rawMaterial.map((post, index) => {
          return (
            <Option value={post.id} key={index}>
              {post.name}
            </Option>
          );
        });
        this.setState({
          SelectMaterial
        });
      }
    });
  }
  //get all for sub category select
  getAllSubCategories() {
    api("GET", "supermix", "/material-sub-categories", "", "", "").then(res => {
      console.log(res.data.results);
      if (res.data.results.materialSubCategories.length > 0) {
        console.log("got sub categories");
        let SelectSubCategories = res.data.results.materialSubCategories.map(
          (post, index) => {
            return (
              <Option value={post.id} key={index}>
                {post.name}
              </Option>
            );
          }
        );
        this.setState({
          SelectSubCategories
        });
      }
    });
  }

  //get all for sub category select
  getAllTest() {
    api("GET", "supermix", "/tests", "", "", "").then(res => {
      console.log(res.data.results);
      if (res.data.results.test.length > 0) {
        console.log("got sub categories");
        let SelectTest = res.data.results.test.map((post, index) => {
          return (
            <Option value={post.id} key={index}>
              {post.name}
            </Option>
          );
        });
        this.setState({
          SelectTest,
          test_name: res.data.results.test[res.data.results.test.length - 1].id
        });
      }
    });
  }

  handleCancel = e => {
    console.log(e);
    this.setState({
      visible: false
    });
  };

  componentWillMount() {
    if (window.screen.width > 1900) {
      console.log("hooray");
      this.setState({
        size: "large"
      });
    } else if (window.screen.width < 1440) {
      this.setState({
        size: "small"
      });
    }
  }

  getallMaterialAcceptedValue = () => {
    api("GET", "supermix", "/admixture-accepted-values", "", "", "").then(
      res => {
        console.log(res.data);
        this.setState({
          admixturedata: res.data.results.admixtureAcceptedValues
        });
      }
    );
  };

  getallAcceptedValue = () => {
    api("GET", "supermix", "/accepted-values", "", "", "").then(res => {
      console.log(res.data);
      this.setState({
        acceptedvaluedata: res.data.results.acceptedValues
      });
    });
  };
  showModal = (record, type) => {
    if (type === "acceptedValue") {
      this.setState({
        switchVal: false,
        visible: true,
        accId: record.id,
        maximum: record.maxValue,
        minimum: record.minValue,
        test_name: record.test.id,
        parameter: record.parameter.id,
        unit: record.unit.id
      });
    } else {
      this.setState({
        switchVal: true,
        visible: true,
        accId: record.acceptedValue.id,
        test_name: record.acceptedValue.test.id,
        parameter: record.acceptedValue.parameter.id,
        unit: record.acceptedValue.unit.id,
        material: record.rawMaterial.id,
        subCategory: record.rawMaterial.materialSubCategory.id,
        switchValMinimum: record.acceptedValue.minValue,
        switchValMaximum: record.acceptedValue.maxValue
      });
    }

    console.log(record);
    console.log(type);
  };

  handleOk = e => {
    console.log(e);
    this.setState({
      visible: false
    });
  };

  handleChange = (pagination, filters, sorter) => {
    console.log("Various parameters", pagination, filters, sorter);
    this.setState({
      filteredInfo: filters,
      sortedInfo: sorter
    });
  };

  clearFilters = () => {
    this.setState({ filteredInfo: null });
  };

  clearAll = () => {
    this.setState({
      filteredInfo: null,
      sortedInfo: null
    });
  };

  setAgeSort = () => {
    this.setState({
      sortedInfo: {
        order: "descend",
        columnKey: "age"
      }
    });
  };

  // plant delete API

  onConfirmdelete(id) {
    console.log(id);
    let mesg = "plant delete";

    api("DELETE", "supermix", "/accepted-value", "", "", id).then(res => {
      console.log(res.data);
      this.getallAcceptedValue();
      Notification("success", res.data.message);
    });
    console.log(this.state.id);
  }

  onConfirmdelete1(id) {
    console.log(id);
    let mesg = "plant delete";

    api("DELETE", "supermix", "/admixture-accepted-value", "", "", id).then(
      res => {
        console.log(res.data);
        this.getallMaterialAcceptedValue();
        Notification("success", res.data.message);
      }
    );
    console.log(this.state.id);
  }

  componentDidMount() {
    this.getAllParameters();
    this.getAllUnits();
    this.getAllMaterials();
    this.getAllSubCategories();
    this.getAllTest();
    this.getallAcceptedValue();
    this.getallMaterialAcceptedValue();
  }

  handleChange = (event, field) => {
    this.setState({ [field]: event.target.value });
    this.setState({ errormgs: "" });
    event.preventDefault();
    const { name, value } = event.target;
    let errors = this.state.errors;
    let optErrors = this.state.optErrors;
    // console.log(name + " is \t" + value);
    switch (name) {
      case "maximum":
        errors.maximum =
          value.length === 0
            ? "maximum can't be empty"
            : isNaN(value)
            ? "maximum won't allow only letters"
            : "";
        break;
      case "minimum":
        errors.minimum =
          value.length === 0
            ? "minimum can't be empty"
            : isNaN(value)
            ? "minimum won't allow only letters"
            : "";
        break;
      case "switchValMaximum":
        optErrors.switchValMaximum =
          value.length === 0
            ? "maximum can't be empty"
            : isNaN(value)
            ? "maximum won't allow only letters"
            : "";
        break;
      case "switchValMinimum":
        optErrors.switchValMinimum =
          value.length === 0
            ? "minimum can't be empty"
            : isNaN(value)
            ? "minimum won't allow only letters"
            : "";
        break;
      default:
        break;
    }

    this.setState({ errors, [name]: value });
  };

  handleSubmit = e => {
    const {
      switchVal,
      errors,
      material,
      maximum,
      minimum,
      parameter,
      subCategory,
      switchValMaximum,
      switchValMinimum,
      unit,
      optErrors,
      test_name,
      accId
    } = this.state;

    if (switchVal) {
      const SwitchedData = {
        id: accId,
        testId: test_name,
        parameterId: parameter,
        unitId: unit,
        rawMaterialId: material,
        maxValue: switchValMaximum,
        minValue: switchValMinimum
      };
      console.log(SwitchedData);
      api(
        "PUT",
        "supermix",
        "/admixture-accepted-value",
        "",
        SwitchedData,
        ""
      ).then(
        res => {
          console.log(res.data.message);
          Notification("success", res.data.message);
          this.getallMaterialAcceptedValue();
          this.getallAcceptedValue();
          this.setState({
            switchVal: false,
            test_name: undefined,
            parameter: undefined,
            unit: undefined,
            maximum: "",
            minimum: "",
            subCategory: undefined,
            material: undefined,
            switchValMaximum: "",
            switchValMinimum: "",
            visible: false,
            errormsgs: ""
          });
        },
        error => {
          console.log(error);
        }
      );
    } else {
      const unSwitchedData = {
        id: accId,
        testId: test_name,
        parameterId: parameter,
        unitId: unit,
        maxValue: maximum,
        minValue: minimum
      };
      console.log(unSwitchedData);
      api("PUT", "supermix", "/accepted-value", "", unSwitchedData, "").then(
        res => {
          console.log(res.data.message);
          Notification("success", res.data.message);
          this.getallAcceptedValue();
          this.setState({
            switchVal: false,
            test_name: undefined,
            parameter: undefined,
            unit: undefined,
            maximum: "",
            minimum: "",
            subCategory: undefined,
            material: undefined,
            switchValMaximum: "",
            switchValMinimum: "",
            visible: false,
            errormsgs: ""
          });
        },
        error => {
          console.log(error);
        }
      );
    }
  };

  render() {
    const {
      switchVal,
      errors,
      material,
      maximum,
      minimum,
      parameter,
      subCategory,
      switchValMaximum,
      switchValMinimum,
      unit,
      optErrors,
      test_name
    } = this.state;
    const { visible, loading } = this.state;
    const columns = [
      {
        title: "Test Name",
        dataIndex: "test.name",
        key: "test.name",
        width: "10%"
      },
      {
        title: "Parameter",
        dataIndex: "parameter.name",
        key: "parameter.name",
        width: "16%"
      },
      {
        title: "Unit",
        dataIndex: "unit.unit",
        key: "unit.unit",
        width: "5%"
      },
      {
        title: "Minimum",
        dataIndex: "minValue",
        key: "tp",
        width: "6%"
      },
      {
        title: "Maximum",
        dataIndex: "maxValue",
        key: "tp",
        width: "6%"
      },

      {
        title: "Edit & Delete",
        key: "action",
        width: "7%",
        render: (text, record = this.state.data) => (
          <span>
            <a>
              <Icon
                type="edit"
                style={{ fontSize: "1.2em" }}
                onClick={this.showModal.bind(this, record, "acceptedValue")}
              />
            </a>
            <Divider type="vertical" />
            <a>
              <Popconfirm
                title="Are you sure you want to Delete this?"
                icon={
                  <Icon type="question-circle-o" style={{ color: "red" }} />
                }
                onConfirm={this.onConfirmdelete.bind(this, record.id)}
              >
                <a href="#">
                  <Icon
                    type="delete"
                    style={{ color: "red", fontSize: "1.2em" }}
                  />
                </a>
              </Popconfirm>
            </a>
          </span>
        )
      }
    ];

    const columns1 = [
      {
        title: "Test Name",
        dataIndex: "acceptedValue.test.name",
        key: "acceptedValue.test.name",
        width: "5%"
      },
      {
        title: "Parameter",
        dataIndex: "acceptedValue.parameter.name",
        key: "acceptedValue.parameter.name",
        width: "15%"
      },
      {
        title: "Unit",
        dataIndex: "acceptedValue.unit.unit",
        key: "acceptedValue.unit.unit",
        width: "4%"
      },
      {
        title: "SubCategory",
        dataIndex: "rawMaterial.materialSubCategory.name",
        key: "rawMaterial.materialSubCategory.name",
        width: "8%"
      },
      {
        title: "Material",
        dataIndex: "rawMaterial.name",
        key: "rawMaterial.name",
        width: "7%"
      },

      {
        title: "Minimum",
        dataIndex: "acceptedValue.minValue",
        key: "acceptedValue.minValue",
        width: "6%"
      },
      {
        title: "Maximum",
        dataIndex: "acceptedValue.maxValue",
        key: "acceptedValue.maxValue",
        width: "6%"
      },
      {
        title: "Edit & Delete",
        key: "action",
        width: "7%",
        render: (text, record = this.state.admixturedata) => (
          <span>
            <a>
              <Icon
                type="edit"
                style={{ fontSize: "1.2em" }}
                onClick={this.showModal.bind(this, record, "admixtureValue")}
              />
            </a>
            <Divider type="vertical" />
            <a>
              <Popconfirm
                title="Are you sure you want to Delete this?"
                icon={
                  <Icon type="question-circle-o" style={{ color: "red" }} />
                }
                onConfirm={this.onConfirmdelete1.bind(this, record.id)}
              >
                <a href="#">
                  <Icon
                    type="delete"
                    style={{ color: "red", fontSize: "1.2em" }}
                  />
                </a>
              </Popconfirm>
            </a>
          </span>
        )
      }
    ];

    return (
      <div style={{ marginTop: "20px" }}>
        <AntTable
          length
          title={() => <AcceptedValueTitle reload={this.getallAcceptedValue} />}
          className="plantManageTable"
          columns={columns}
          dataSource={this.state.acceptedvaluedata}
          onChange={this.handleChange}
          pagination={{ defaultPageSize: 7 }}
          size={this.state.size}
        />

        <AntTable
          length
          title={() => (
            <AdmixtureValueTitle reload={this.getallMaterialAcceptedValue} />
          )}
          className="plantManageTable"
          columns={columns1}
          dataSource={this.state.admixturedata}
          onChange={this.handleChange}
          pagination={{ defaultPageSize: 7 }}
          size={this.state.size}
        />

        <Modal
          visible={visible}
          closable={false}
          loading={loading}
          // style={{ width: 1500 }}
          onCancel={this.handleCancel}
          title={
            <MasterLevelFormTitle>
              <p
                style={{
                  color: "white"
                }}
              >
                {this.state.type === "edit" ? "Edit Size" : "Add Size"}
              </p>
              <Icon
                type="close-circle"
                onClick={this.handleCancel}
                style={{
                  color: "white"
                }}
              />
            </MasterLevelFormTitle>
          }
          footer={[
            <Button key="back" onClick={this.handleCancel}>
              Cancel
            </Button>,
            <PrimaryButton
              key="submit"
              loading={loading}
              onClick={e => this.handleSubmit(e)}
              style={{ background: "#001328", color: "white", border: "none" }}
            >
              {this.state.type === "edit" ? "Edit" : "Save"}
            </PrimaryButton>
          ]}
          width="600px"
        >
          <FlexContainer stepsarea style={{ marginTop: "40px" }}>
            <div testconfig>
              <MasterLevelForm
                filled
                borderRadiused
                style={{
                  justifyContent: "space-around",
                  paddingBottom: "20px",
                  paddingLeft: "40px",
                  paddingRight: "60px",
                  paddingTop: "0px",
                  width: "600px"
                }}
              >
                {/* Test */}
                <div className="input_wrapper">
                  <label for="test_name" className="label">
                    Test
                  </label>
                  <Select
                    id="test_name"
                    name="test_name"
                    placeholder="Select Test"
                    style={{ width: 170 }}
                    value={test_name}
                    onChange={value => this.handleSelect("test_name", value)}
                  >
                    {this.state.SelectTest}
                  </Select>
                  <div style={{ height: "6px" }}></div>
                </div>

                {/* Parameter */}
                <div className="input_wrapper">
                  <label for="parameter" className="label">
                    Parameter
                  </label>
                  <Select
                    id="parameter"
                    name="parameter"
                    placeholder="Select Parameter"
                    style={{ width: 170 }}
                    value={parameter}
                    onChange={value => this.handleSelect("parameter", value)}
                  >
                    {this.state.SelectParameter}
                  </Select>
                  {errors.parameter.length > 0 && (
                    <div style={error}>{errors.parameter}</div>
                  )}
                  <div style={{ height: "6px" }}></div>
                </div>

                {/* Unit */}
                <div className="input_wrapper">
                  <label for="unit" className="label">
                    Unit
                  </label>
                  <Select
                    id="unit"
                    name="unit"
                    placeholder="Select Unit"
                    style={{ width: 170 }}
                    value={unit}
                    onChange={value => this.handleSelect("unit", value)}
                  >
                    {this.state.SelectUnit}
                  </Select>
                  {errors.unit.length > 0 && (
                    <div style={error}>{errors.unit}</div>
                  )}
                  <div style={{ height: "6px" }}></div>
                </div>

                {/* Test Max and Min */}
                {switchVal === false ? (
                  <FlexContainer>
                    <div className="input_wrapper">
                      <PrimaryButton type="ghost">Between</PrimaryButton>
                      <div style={{ height: "18px" }}></div>
                    </div>
                    {/* Maximum */}
                    <div className="input_wrapper">
                      <Input
                        id="maximum"
                        name="maximum"
                        placeholder="Maximum"
                        style={{ width: 90 }}
                        value={maximum}
                        onChange={this.handleChange}
                      />
                      {errors.maximum.length > 0 && (
                        <div style={minierror}>{errors.maximum}</div>
                      )}
                      <div style={{ height: "12px" }}></div>
                    </div>
                    <div className="input_wrapper">
                      <PrimaryButton type="ghost">To</PrimaryButton>
                      <div style={{ height: "12px" }}></div>
                    </div>
                    {/* Minimum */}
                    <div className="input_wrapper">
                      <Input
                        id="minimum"
                        name="minimum"
                        placeholder="Minimum"
                        style={{ width: 90 }}
                        value={minimum}
                        onChange={this.handleChange}
                      />
                      {errors.minimum.length > 0 && (
                        <div style={minierror}>{errors.minimum}</div>
                      )}
                      <div style={{ height: "12px" }}></div>
                    </div>
                  </FlexContainer>
                ) : (
                  ""
                )}

                <FlexContainer>
                  {/* Material Switch */}
                  <div
                    className="input_wrapper"
                    style={{ marginTop: switchVal === false ? "5px" : "20px" }}
                  >
                    <label for="material_switch" className="label">
                      {switchVal === false ? "Test" : "Material"}
                    </label>
                    <Switch
                      onChange={checked => this.onChange(checked)}
                      value={switchVal}
                    />
                    <div style={{ height: "12px" }}></div>
                  </div>
                </FlexContainer>

                {switchVal === true ? (
                  <FlexContainer home>
                    {/* Sub Category */}
                    <div className="input_wrapper">
                      <label for="subCategory" className="label">
                        Sub Category
                      </label>
                      <Select
                        id="subCategory"
                        name="subCategory"
                        placeholder="Select Sub Category"
                        style={{ width: 170 }}
                        value={subCategory}
                        onChange={value =>
                          this.handleSelect("subCategory", value)
                        }
                      >
                        {this.state.SelectSubCategories}
                      </Select>
                      {optErrors.subCategory.length > 0 && (
                        <div style={error}>{optErrors.subCategory}</div>
                      )}
                      <div style={{ height: "12px" }}></div>
                    </div>

                    {/* Material */}
                    <div className="input_wrapper">
                      <label for="material" className="label">
                        Material
                      </label>
                      <Select
                        id="material"
                        name="material"
                        placeholder="Select Material"
                        style={{ width: 160 }}
                        value={material}
                        onChange={value => this.handleSelect("material", value)}
                      >
                        {this.state.SelectMaterial}
                      </Select>
                      {optErrors.material.length > 0 && (
                        <div style={error}>{optErrors.material}</div>
                      )}
                      <div style={{ height: "12px" }}></div>
                    </div>
                  </FlexContainer>
                ) : (
                  ""
                )}
                {switchVal === true ? (
                  <FlexContainer home>
                    <div className="input_wrapper">
                      <PrimaryButton type="ghost">Between</PrimaryButton>
                      <div style={{ height: "12px" }}></div>
                    </div>
                    {/* Maximum  */}
                    <div className="input_wrapper">
                      <Input
                        id="switchValMaximum"
                        name="switchValMaximum"
                        placeholder="maximum"
                        style={{ width: 90 }}
                        value={switchValMaximum}
                        onChange={this.handleChange}
                      />
                      {optErrors.switchValMaximum.length > 0 && (
                        <div style={minierror}>
                          {optErrors.switchValMaximum}
                        </div>
                      )}
                      <div style={{ height: "12px" }}></div>
                    </div>

                    <div className="input_wrapper">
                      <PrimaryButton type="ghost">To</PrimaryButton>
                      <div style={{ height: "12px" }}></div>
                    </div>

                    {/* Minimum  */}
                    <div className="input_wrapper">
                      <Input
                        id="switchValMinimum"
                        name="switchValMinimum"
                        placeholder="minimum"
                        style={{ width: 90 }}
                        value={switchValMinimum}
                        onChange={this.handleChange}
                      />
                      {optErrors.switchValMinimum.length > 0 && (
                        <div style={minierror}>
                          {optErrors.switchValMinimum}
                        </div>
                      )}
                      <div style={{ height: "12px" }}></div>
                    </div>
                  </FlexContainer>
                ) : (
                  ""
                )}
              </MasterLevelForm>
            </div>
          </FlexContainer>
        </Modal>
      </div>
    );
  }
}

export default AcceptedValue;
