import React, { Component } from "react";
import { Input, Modal, Button, Icon, Checkbox } from "antd";
import { api } from "../../services/AxiosService";
import { PrimaryButton } from "../../styledcomponents/button/button";
import {
  MasterLevelForm,
  MasterLevelFormTitle
} from "../../styledcomponents/form/MasterLevelForms";
import Notificationfuc from "../../Constant/Notification";
import { connect } from "react-redux";
import { DISABLE_EDIT_MODE } from "../../../redux/action/master/plantlevel/PlantLevel";
import { Main_Category_Name_Aggregate } from "../../Constant/Api";

const error = {
  color: "red",
  fontSize: "12px",
  width: "160px",
  height: "2px"
};

class AddSize extends Component {
  constructor(props) {
    super(props);

    this.state = {
      loading: false,
      visible: false,
      errors: {
        material_sub_category_id: "",
        size: ""
      },
      material_sub_category_name: "",
      material_sub_category_id: "",
      size: "",
      errormgs: "",
      type: "add"
    };
  }

  onChangeCheck = checkedValues => {
    console.log("checked = ", checkedValues);
    this.setState({
      material_sub_category_id: checkedValues
    });
  };
  componentDidMount() {
    this.getSubCategoryByMain();
  }
  getSubCategoryByMain = () => {
    let optData = [];
    api(
      "GET",
      "supermix",
      "/material-sub-category/materialCategoryName",
      "",
      "",
      Main_Category_Name_Aggregate
    ).then(res => {
      let dataSub = res.data.results.materialSubCategories;
      console.log(dataSub);
      for (var i = 0; i < dataSub.length; i++) {
        optData.push({
          label: dataSub[i].name,
          value: dataSub[i].id
        });
      }
      console.log(optData);
      this.setState({
        optData
      });
    });
  };

  handleChange = (event, field) => {
    this.setState({ [field]: event.target.value });
    this.setState({ errormgs: "" });
    event.preventDefault();
    const { name, value } = event.target;
    let errors = this.state.errors;
    console.log(name + " is \t" + value);
    switch (name) {
      case "size":
        errors.size = value.length === 0 ? "size can't be empty" : "";
        break;
      default:
        break;
    }

    this.setState({ errors, [name]: value });
  };

  handleSubmit = event => {
    event.preventDefault();
    if (
      this.state.size.length === 0 &&
      this.state.material_sub_category_name.length === 0
    ) {
      this.setState({
        errors: {
          size: "Size can't be empty",
          material_sub_category_name: "Sub category  can't be empty"
        }
      });
    } else if (
      this.state.size.length === 0 &&
      this.state.errors.size.length === 0
    ) {
      this.setState({
        errors: {
          size: this.state.size || "Size can't be empty",
          material_sub_category_name: this.state.material_sub_category_id
        }
      });
    } else if (
      this.state.material_sub_category_id.length === 0 &&
      this.state.errors.material_sub_category_id.length === 0
    ) {
      this.setState({
        errors: {
          size: this.state.size,
          material_sub_category_name:
            this.state.material_sub_category_id ||
            "Sub category  can't be empty"
        }
      });
    } else if (
      this.state.errors.size.length === 0 &&
      this.state.errors.material_sub_category_id.length === 0
    ) {
      console.log(this.state.errors);

      let data = [];
      for (var i = 0; i < this.state.material_sub_category_id.length; i++) {
        data.push({
          size: this.state.size,
          materialSubCategoryId: this.state.material_sub_category_id[i]
        });
      }
      // const data = {
      //   size: this.state.size,
      //   materialSubCategoryId: this.state.material_sub_category_id[0]
      // };
      if (this.state.type === "add") {
        api("POST", "supermix", "/sieve-size", "", data, "")
          .then(
            res => {
              console.log(res.data);

              Notificationfuc("success", res.data.message);
              this.props.reload();
              // this.setState({ loading: true });
              this.setState({
                // material_sub_category_id: "",
                // size: "",
                errormgs: ""
              });
            },
            error => {
              // this.setState({
              //   errormgs: error.validationFailures[0]
              // });
              console.log("DEBUG34: ", error);
              // console.log(HandelError(error.validationFailures[0]));
            }
          )
          .catch(error => {
            this.setState({
              // errormgs: "Plant Name Exist"
            });
            console.log(error);
          });
      }
      // else {
      //   const data = {
      //     code: this.state.plant_code,
      //     name: this.state.plant_name,
      //     address: this.state.plant_address,
      //     phoneNumber: this.state.plant_contactno
      //   };
      //   api("PUT", "supermix", "/plant", "", data, "")
      //     .then(
      //       res => {
      //         console.log(res.data);

      //         if (res.data.status == "VALIDATION_FAILURE") {
      //           console.log("update");
      //           this.responeserror(res.data.results.name.message);
      //         } else {
      //           Notificationfuc("success", res.data.message);
      //           this.props.reload();
      //           this.setState({ loading: true });
      //           this.setState({
      //             plant_code: "",
      //             plant_name: "",
      //             plant_address: "",
      //             plant_contactno: "",
      //             errormgs: ""
      //           });
      //           setTimeout(() => {
      //             this.setState({ loading: false, visible: false });
      //           }, 3000);
      //         }
      //       },
      //       error => {
      //         this.setState({
      //           errormgs: error.validationFailures[0]
      //         });
      //         console.log("DEBUG34: ", error);
      //         console.log(HandelError(error.validationFailures[0]));
      //       }
      //     )
      //     .catch(error => {
      //       // this.setState({
      //       //   errormgs: "Plant Name Exist"
      //       // });
      //       // console.log(error.response.data);
      //     });
      // }
      console.log(data);
      console.log("form is valid");
    }
  };

  showModal = () => {
    this.setState({
      visible: true,
      size: "",
      material_sub_category_id: "",
      errors: {
        size: "",
        material_sub_category_id: ""
      }
    });
  };

  handleCancel = () => {
    if (this.state.type === "edit") {
      // we call the redux function to dispatch and delete all the global redux state to close the modal
      this.props.setPlantVisiblity();
    }

    this.setState({
      visible: false,
      formValid: false,
      errors: {
        size: "",
        material_sub_category_id: ""
      },
      size: "",
      material_sub_category_id: "",
      errormgs: ""
    });
  };

  componentWillReceiveProps(nextProps) {
    // this.setState({
    //   visible: nextProps.visible,
    //   plant_code: nextProps.editPlantData.code,
    //   plant_name: nextProps.editPlantData.name,
    //   plant_address: nextProps.editPlantData.address,
    //   plant_contactno: nextProps.editPlantData.phoneNumber,
    //   type: nextProps.type
    // });
  }

  render() {
    const { visible, loading } = this.state;
    const { errors } = this.state;
    return (
      <div>
        <PrimaryButton
          onClick={this.showModal}
          style={{
            background: "#001328",
            color: "white",
            border: "none",
            width: "120px",
            marginLeft: "-10px"
          }}
        >
          Add Size
        </PrimaryButton>
        <Modal
          visible={visible}
          closable={false}
          loading={loading}
          onCancel={this.handleCancel}
          title={
            <MasterLevelFormTitle>
              <p
                style={{
                  color: "white"
                }}
              >
                {this.state.type === "edit" ? "Edit Size" : "Add Size"}
              </p>
              <Icon
                type="close-circle"
                onClick={this.handleCancel}
                style={{
                  color: "white"
                }}
              />
            </MasterLevelFormTitle>
          }
          footer={[
            <Button key="back" onClick={this.handleCancel}>
              Cancel
            </Button>,
            <PrimaryButton
              key="submit"
              loading={loading}
              onClick={this.handleSubmit}
              style={{ background: "#001328", color: "white", border: "none" }}
            >
              {this.state.type === "edit" ? "Edit" : "Add More"}
            </PrimaryButton>
          ]}
          width="600px"
        >
          <MasterLevelForm style={{ justifyContent: "space-evenly" }}>
            <div className="input_wrapper">
              <label for="sub_category" className="label">
                Material Sub Category:
              </label>
              <Checkbox.Group
                options={this.state.optData}
                onChange={this.onChangeCheck}
                value={this.state.material_sub_category_id}
              />
              <div style={{ height: "12px" }}></div>
            </div>
            <div className="input_wrapper">
              <label for="plant_code" className="label">
                Size:
              </label>
              <Input
                id="size"
                name="size"
                placeholder="Enter the size "
                value={this.state.size}
                onChange={this.handleChange}
              />

              {errors.size.length > 0 && <div style={error}>{errors.size}</div>}

              <div style={{ height: "12px" }}></div>
            </div>
          </MasterLevelForm>
        </Modal>
      </div>
    );
  }
}

const mapStateToProps = state => {
  //getting the global redux state to get the data from the EditPlantReducer.js
  return {
    visible: state.plantLevelReducers.EditPlantReducer.visible,
    type: state.plantLevelReducers.EditPlantReducer.type,
    editPlantData: state.plantLevelReducers.EditPlantReducer.editPlantData
  };
};

const mapDispatchToProps = dispatch => {
  return {
    // setting visible to false if we close the modal .. and all state data will be deleted if this function is dispatched
    setPlantVisiblity: () => {
      dispatch({ type: DISABLE_EDIT_MODE });
      console.log("edit modal closed");
    }
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(AddSize);
