import React, { Component} from "react";
import { Icon, Popconfirm, Divider } from "antd";
import { AntTable } from "../../styledcomponents/table/AntTabl";
import { api } from "../../services/AxiosService";
import Notification from "../../Constant/Notification";
import { connect } from "react-redux";
import { SWITCH_TO_EDIT_MODE } from "../../../redux/action/master/plantlevel/PlantLevel";
import FinenessTitle from "./title/FInenessTitle";

class FinenessModules extends Component {
  state = {
    searchText: "",
    visible: false,
    size: "small",
    data: "",
    id: ""
  };

  componentWillMount() {
    if (window.screen.width > 1900) {
      console.log("hooray");
      this.setState({
        size: "large"
      });
    } else if (window.screen.width < 1440) {
      this.setState({
        size: "small"
      });
    }
  }
  componentDidMount() {
    this.getallPlant();
  }

  //  get all plant API
  getallPlant = () => {
    let datalist = [];
    api("GET", "supermix", "/fineness-modulus", "", "", "").then(res => {
      console.log(res.data);
      let x = res.data.results.finenessModulus;
      for (var i = 0; i < x.length; i++) {
        datalist.push({
          id: x[i].id,
          min: x[i].min,
          max: x[i].max,
          materialSubCategoryName: x[i].materialSubCategory.name
        });
      }
      this.setState({
        datalist
      });
    });
  };

  showModal = () => {
    this.setState({
      visible: true
    });
  };

  handleOk = e => {
    console.log(e);
    this.setState({
      visible: false
    });
  };

  handleCancel = e => {
    console.log(e);
    this.setState({
      visible: false
    });
  };

  handleChange = (pagination, filters, sorter) => {
    console.log("Various parameters", pagination, filters, sorter);
    this.setState({
      filteredInfo: filters,
      sortedInfo: sorter
    });
  };

  clearFilters = () => {
    this.setState({ filteredInfo: null });
  };

  clearAll = () => {
    this.setState({
      filteredInfo: null,
      sortedInfo: null
    });
  };

  setAgeSort = () => {
    this.setState({
      sortedInfo: {
        order: "descend",
        columnKey: "age"
      }
    });
  };

  onChange(pageNumber) {
    console.log("Page: ", pageNumber);
  }

  // plant delete API

  onConfirmdelete(code) {
    console.log(code);
    api("DELETE", "supermix", "/fineness-modulus", "", "", code).then(res => {
      console.log(res.data);
      this.getallPlant();
      Notification("success", res.data.message);
    });
    console.log(this.state.id);
  }

  render() {
    const columns = [
      {
        title: "SubCategory",
        dataIndex: "materialSubCategoryName",
        key: "action",
        width: "7%"
      },
      {
        title: "Minimum Value",
        dataIndex: "min",
        key: "unit",
        width: "7%"
      },
      {
        title: "Maximum Value",
        dataIndex: "max",
        key: "action",
        width: "7%"
      },

      {
        title: "Edit & Delete",
        key: "action",
        width: "7%",
        render: (text, record = this.state.datalist) => (
          <span>
            <a>
              <Icon
                type="edit"
                style={{ fontSize: "1.2em" }}
                // onClick={this.props.passEditincomingRecordToModal.bind(
                //   this,
                //   record
                // )}
              />
            </a>
            <Divider type="vertical" />
            <a>
              <Popconfirm
                title="Are you sure you want to Delete this?"
                icon={
                  <Icon type="question-circle-o" style={{ color: "red" }} />
                }
                onConfirm={this.onConfirmdelete.bind(this, record.id)}
              >
                <a href="#">
                  <Icon
                    type="delete"
                    style={{ color: "red", fontSize: "1.2em" }}
                  />
                </a>
              </Popconfirm>
            </a>
          </span>
        )
      }
    ];

    return (
      <AntTable
        length
        title={() => <FinenessTitle reload={this.getallPlant} />}
        className="plantManageTable"
        columns={columns}
        dataSource={this.state.datalist}
        onChange={this.handleChange}
        pagination={{ defaultPageSize: 7 }}
        size={this.state.size}
      />
    );
  }
}

const mapStateToProps = state => null;

const mapDispatchToProps = dispatch => {
  return {
    // if this function dispatches modal will be shown and the data will be drawn :)
    passEditPlantRecordtoModal: record => {
      //this payload is the data we pass into redux which is in the row which we clicked
      dispatch({ type: SWITCH_TO_EDIT_MODE, payload: record });
      console.log(record);
    }
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(FinenessModules);
