import React, { Component } from "react";
import { Popconfirm, Divider, Icon, Modal, Table } from "antd";

import { AntTable } from "../../../styledcomponents/table/AntTabl";
import SieveTestTitle from "../titles/RawMaterialTitle";
import { api } from "../../../services/AxiosService";
import { FlexContainer } from "../../../styledcomponents/container/FlexGrid";
import Notification from "../../../Constant/Notification";

export default class ManageSieveTest extends Component {
  state = {
    filteredInfo: null,
    sortedInfo: null,
    searchText: "",
    visible: false,
    size: "small",
    materialTests: [],
    filteredTestTrials: []
  };

  getAllMaterialTestTrials = () => {
    api("GET", "supermix", "/sieve-tests", "", "", "").then(res => {
      console.log(res.data.results.sieveTests);
      this.setState({
        materialTests: res.data.results.sieveTests
      });
    });
  };

  componentDidMount() {
    this.getAllMaterialTestTrials();
  }

  componentWillMount() {
    if (window.screen.width > 1900) {
      console.log("hooray");
      this.setState({
        size: "large"
      });
    } else if (window.screen.width < 1440) {
      this.setState({
        size: "small"
      });
    }
  }

  getalltestTrailByTestId = code => {
    this.setState({
      visible: true
    });
    let resultData = [];
    api("GET", "supermix", "/sieve-test-trial/sieve-test", "", "", code).then(
      res => {
        let x = res.data.results.sieveTestCode;
        console.log(res.data.results.sieveTestCode);
        for (var i = 0; i < x.length; i++) {
          resultData.push({
            size: x[i].sieveSize.size,
            percentageRetained: x[i].percentageRetained,
            cumulativeRetained: x[i].cumulativeRetained,
            passing: x[i].passing,
            totalWeight: x[i].sieveTest.totalWeight,
            status: x[i].sieveTest.status
          });
        }
        if (resultData.length > 0) {
          this.setState({
            totalWeight: resultData[0].totalWeight,
            status: resultData[0].status
          });
        }

        this.setState({
          resultData
        });
      }
    );
  };

  showModal = () => {
    this.setState({
      visible: true
    });
  };

  handleOk = e => {
    console.log(e);
    this.setState({
      visible: false
    });
    this.state.filteredTestTrials.splice(
      0,
      this.state.filteredTestTrials.length
    );
    console.log(this.state.filteredTestTrials);
  };

  handleCancel = e => {
    console.log(e);
    this.setState({
      visible: false
    });
    this.state.filteredTestTrials.splice(
      0,
      this.state.filteredTestTrials.length
    );
    console.log(this.state.filteredTestTrials);
  };

  handleChange = (pagination, filters, sorter) => {
    console.log("Various parameters", pagination, filters, sorter);
    this.setState({
      filteredInfo: filters,
      sortedInfo: sorter
    });
  };

  clearFilters = () => {
    this.setState({ filteredInfo: null });
  };

  clearAll = () => {
    this.setState({
      filteredInfo: null,
      sortedInfo: null
    });
  };

  setAgeSort = () => {
    this.setState({
      sortedInfo: {
        order: "descend",
        columnKey: "code"
      }
    });
  };

  onChange(pageNumber) {
    console.log("Page: ", pageNumber);
  }
  onConfirmdelete(id) {
    console.log(id);

    api("DELETE", "supermix", "/sieve-test", "", "", id).then(res => {
      console.log(res.data);
      this.getAllMaterialTestTrials();
      Notification("success", res.data.message);
    });
  }

  render() {
    const columns = [
      {
        title: "Code",
        dataIndex: "code",
        key: "code"
      },
      {
        title: "Date",
        dataIndex: "date",
        key: "date"
      },
      {
        title: "FinenessModulus",
        dataIndex: "finenessModulus",
        key: "finenessModulus"
      },
      {
        title: "Pan Weight",
        dataIndex: "panWeight",
        key: "panWeight"
      },
      {
        title: "Total Weight",
        dataIndex: "totalWeight",
        key: "totalWeight"
      },
      {
        title: "Status",
        dataIndex: "status",
        key: "status"
      },

      {
        title: "Trials",
        key: "trials",
        render: (text, record = this.state.materialTests) => (
          <Icon
            type="container"
            style={{ color: "green" }}
            onClick={this.getalltestTrailByTestId.bind(this, record.code)}
          />
        )
      },
      {
        title: "Edit & Delete",
        key: "action",
        width: "7%",
        render: (text, record = this.state.datalist) => (
          <span>
            <a>
              {/* <Icon
                type="edit"
                onClick={this.props.passEditSubManageCategoryToModal.bind(
                  this,
                  record
                )}
              /> */}
            </a>
            <Divider type="vertical" />
            <a>
              <Popconfirm
                title="Are you sure you want to Delete this?"
                icon={
                  <Icon type="question-circle-o" style={{ color: "red" }} />
                }
                onConfirm={this.onConfirmdelete.bind(this, record.code)}
              >
                <a href="#">
                  <Icon type="delete" style={{ color: "red" }}></Icon>
                </a>
              </Popconfirm>
            </a>
          </span>
        )
      }
    ];

    const Columns1 = [
      {
        title: "Sieve Size (mm)",
        dataIndex: "size",
        key: "size",
        width: "15%"
      },
      {
        title: "Cummalative Weight Retained (g)",
        dataIndex: "cumulativeRetained",
        key: "cumulativeRetained"
      },

      {
        title: "Percentage Weight  Retained",
        dataIndex: "percentageRetained",
        key: "percentageRetained"
      },
      {
        title: "Percentage Weight Passing",
        dataIndex: "passing",
        key: "passing"
      }
    ];

    return (
      <div>
        <AntTable
          title={() => <h3>Sieve Test Results</h3>}
          maxlength
          nomargin
          columns={columns}
          dataSource={this.state.materialTests}
          onChange={this.handleChange}
          pagination={{ defaultPageSize: 10 }}
          size={this.state.size}
        />
        <Modal
          title="Basic Modal"
          visible={this.state.visible}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
          width="950px"
        >
          <Table
            columns={Columns1}
            dataSource={this.state.resultData}
            style={{
              background: "white",
              width: "1250px",
              marginTop: "-10px"
            }}
            scroll={{ y: 280 }}
            size="small"
            style={{
              width: "900px"
              // marginTop: "10px",
              // marginLeft: "80px"
            }}
            pagination={false}
          />
        </Modal>
      </div>
    );
  }
}
