import React, { Component } from "react";
import { FlexContainer } from "../../styledcomponents/container/FlexGrid";
import SieveSize from "../../test/sievetest/SieveSize";
import SieveAcceptedValue from "../../test/sievetest/SieveAcceptedValue";
import SieveTestMaster from "../../test/sievetest/SieveMaster";
import FinessModulus from "../../test/sievetest/FinesModulus";

export default class Landing extends Component {
  state = {
    type: ""
  };

  typechange = type => {
    console.log(type);
    this.setState({ type: type });
  };

  render() {
    return (
      <FlexContainer leveltileareafixed>
        <SieveTestMaster type={this.typechange} />

        {this.state.type === "sieveSize" ? (
          <SieveSize />
        ) : this.state.type === "FinessModulus" ? (
          <FinessModulus />
        ) : (
          <SieveAcceptedValue />
        )}
      </FlexContainer>
    );
  }
}
