import {
  ROUTE_TO_ACCEPTED_VALUE,
  ROUTE_TO_TEST_PARAMETER,
  ROUTE_TO_EQUATION
} from "../../action/table/Table";

const initialState = {
  routepath: "/acceptedvalue"
};

export const RoutingBetweenTables = (state = initialState, action) => {
  switch (action.type) {
    case ROUTE_TO_ACCEPTED_VALUE:
      return {
        ...state,
        routepath: (state.routepath = "/acceptedvalue")
      };
    case ROUTE_TO_TEST_PARAMETER:
      return {
        ...state,
        routepath: (state.routepath = "/testparameter")
      };
    case ROUTE_TO_EQUATION:
      return {
        ...state,
        routepath: (state.routepath = "/equation")
      };

    default:
      return state;
  }
};
