import React, { Component } from "react";
import { Icon } from "antd";
import BasicCard from "../../styledcomponents/card/BasicCard";
import {
  TileArea,
  TileAreaText,
  TileAreaAction
} from "../../styledcomponents/card/TileArea";

class SieveTestMaster extends Component {
  constructor(props) {
    super(props);
    this.state = {
      status: true
    };
  }
  goSieveSize = () => {
    this.props.type("sieveSize");
  };

  goSieveAcceptedValues = () => {
    this.props.type("sieveAcceptedsize");
  };

  goFinessModulus = () => {
    this.props.type("FinessModulus");
  };

  render() {
    return (
      <TileArea>
        <BasicCard
          finalproduct
          tileareacard
          size="small"
          hoverable={true}
          onClick={this.goSieveSize}
        >
          <TileAreaText>Sieve Size</TileAreaText>
          <TileAreaAction>
            <Icon
              filled
              type="plus"
              style={{ color: "red", fontSize: "22px" }}
            />
          </TileAreaAction>
        </BasicCard>
        <BasicCard
          finalproduct
          tileareacard
          // size='small'
          hoverable={true}
          onClick={this.goSieveAcceptedValues}
        >
          <TileAreaText testreport unit>
            Sieve Accepted Value
          </TileAreaText>
          <TileAreaAction>
            <Icon
              filled
              type="plus"
              style={{ color: "red", fontSize: "22px" }}
            />
          </TileAreaAction>
        </BasicCard>
        <BasicCard
          finalproduct
          tileareacard
          size="small"
          hoverable={true}
          onClick={this.goFinessModulus}
        >
          <TileAreaText materialcategory unit>
            Fineness Modulus
          </TileAreaText>
          <TileAreaAction>
            <Icon
              filled
              type="plus"
              style={{ color: "red", fontSize: "22px" }}
            />
          </TileAreaAction>
        </BasicCard>
      </TileArea>
    );
  }
}

// const mapStateToProps = state => {};

export default SieveTestMaster;
