/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { Component } from "react";
import { AntTable } from "../../styledcomponents/table/AntTabl";
import history from "../../../Components/Constant/history";
import {  Button,Tag } from "antd";
import { api } from "../../services/AxiosService";

export default class ManageSieveTest extends Component {
  state = {
    sampleData: ""
  };
  componentDidMount() {
    console.log(this.props.testId);
    this.getAll();
  }
  testConfig = record => {
    history.push(`/sieveTrial/${record.code}`);

    console.log(record);
  };

  getAll = () => {
    const datalist = [];
    api("GET", "supermix", "/incoming-samples", "", "", "").then(res => {
      console.log(res.data);
      res.data.results.incomingSamples.map((post, index) => {
        datalist.push({
          code: post.code,
          vehicleNo: post.vehicleNo,
          date: post.date,
          status: post.status,
          rawMaterialName: post.rawMaterial.name,
          nature: post.rawMaterial.nature,
          materialSubCategoryName: post.rawMaterial.materialSubCategory.name,
          plantName: post.plant.name,
          supplierName: post.supplier.name
        });
      });
      this.setState({
        sampleData: datalist
      });
    });
  };
  render() {
    const columns = [
      {
        title: "Sample Code",
        dataIndex: "code",
        key: "code",
        width: "10%"
      },
      {
        title: "Name",
        dataIndex: "rawMaterialName",
        key: "rawMaterialName",
        width: "10%"
      },
      {
        title: "Vehicle Number",
        dataIndex: "vehicleNo",
        key: "vehicleNo",
        width: "10%"
      },
      {
        title: "Date",
        dataIndex: "date",
        key: "date",
        width: "10%"
      },
      {
        title: "Plant",
        dataIndex: "plantName",
        key: "plantName",
        width: "12%"
      },
      {
        title: "Supplier",
        dataIndex: "supplierName",
        key: "supplieName",
        width: "12%"
      },
      {
        title: "Status",
        dataIndex: "status",
        key: "status",
        width: "10%",
        render: status => (
          <span>
            {
              <Tag
                color={
                  status === "PASS"
                    ? "green"
                    : status === "PROCESS"
                    ? "orange"
                    : status === "NEW"
                    ? "yellow"
                    : "red"
                }
              >
                {status}
              </Tag>
            }
          </span>
        )
      },
      {
        title: "Test",
        key: "test",
        width: "15%",
        render: (record = this.state.sampleData) => (
          <Button onClick={this.testConfig.bind(this, record)}>test</Button>
        )
      }
    ];
    return (
      <AntTable
        title={() => (
          <div style={{ height: "20px" }}>
            {/* <p style={styleObj}>{title}</p> */}
          </div>
        )}
        columns={columns}
        dataSource={this.state.sampleData}
        style={{ background: "white", width: "1300px", marginTop: "-10px" }}
        size="small"
        onRow={(record, rowIndex) => {
          return {
            // onClick: event => {testConfig(record)}, // click row
            onDoubleClick: event => {
              this.testConfig(record);
            } // double click row
            // onContextMenu: event => {}, // right button click row
            // onMouseEnter: event => {}, // mouse enter row
            // onMouseLeave: event => {}, // mouse leave row
          };
        }}
      />
    );
  }
}