import React, { Component } from "react";
import {
  TileArea,
  TileAreaText,
  TileAreaAction
} from "../../../../styledcomponents/card/TileArea";
import {
  ROUTE_TO_ACCEPTED_VALUE,
  ROUTE_TO_TEST_PARAMETER,
  ROUTE_TO_EQUATION
} from "../../../../../redux/action/table/Table";
import BasicCard from "../../../../styledcomponents/card/BasicCard";
import { Icon } from "antd";
import { connect } from "react-redux";

class TableTileArea extends Component {
  render() {
    return (
      <TileArea style={{ marginTop: "30px" }}>
        <BasicCard
          finalproduct
          tileareacard
          size="small"
          hoverable={true}
          onClick={this.props.routeToAcceptedValue}
        >
          <TileAreaText testreport unit>
            Manage Accepted Value
          </TileAreaText>
          <TileAreaAction>
            <Icon
              filled
              type="plus"
              style={{ color: "red", fontSize: "22px" }}
            />
          </TileAreaAction>
        </BasicCard>
        <BasicCard
          finalproduct
          tileareacard
          size="small"
          hoverable={true}
          onClick={this.props.routeToTestparameter}
        >
          <TileAreaText testreport unit>
            Manage Test Parameter
          </TileAreaText>
          <TileAreaAction>
            <Icon
              filled
              type="plus"
              style={{ color: "red", fontSize: "22px" }}
            />
          </TileAreaAction>
        </BasicCard>
        <BasicCard
          finalproduct
          tileareacard
          size="small"
          hoverable={true}
          onClick={this.props.routeToEquation}
        >
          <TileAreaText testreport unit>
            Manage Equation
          </TileAreaText>
          <TileAreaAction>
            <Icon
              filled
              type="plus"
              style={{ color: "red", fontSize: "22px" }}
            />
          </TileAreaAction>
        </BasicCard>
      </TileArea>
    );
  }
}
const mapStateToProps = state => {};

const mapDispatchToProps = dispatch => {
  return {
    routeToAcceptedValue: () => {
      dispatch({ type: ROUTE_TO_ACCEPTED_VALUE });
      console.log("ROUTE_TO_ACCEPTED_VALUE click dispatched");
    },
    routeToTestparameter: () => {
      dispatch({ type: ROUTE_TO_TEST_PARAMETER });
      console.log("ROUTE_TO_TEST_PARAMETER click dispatched");
    },
    routeToEquation: () => {
      dispatch({ type: ROUTE_TO_EQUATION });
      console.log("ROUTE_TO_EQUATION click dispatched");
    }
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(TableTileArea);
