/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { Component } from "react";
import { Icon, Popconfirm, Divider } from "antd";
import EquationTitle from "../ManageTables/titles/ManageEquation";
import { AntTable } from "../../../../styledcomponents/table/AntTabl";
import { api } from "../../../../services/AxiosService";
import Notification from "../../../../Constant/Notification";
import { connect } from "react-redux";
import { SWITCH_TO_EDIT_MODE } from "../../../../../redux/action/master/plantlevel/PlantLevel";

class ManageEquation extends Component {
  state = {
    searchText: "",
    visible: false,
    size: "small",
    data: "",
    id: ""
  };

  componentWillMount() {
    if (window.screen.width > 1900) {
      console.log("hooray");
      this.setState({
        size: "large"
      });
    } else if (window.screen.width < 1440) {
      this.setState({
        size: "small"
      });
    }
  }
  componentDidMount() {
    this.getallEquation();
  }

  //  get all plant API
  getallEquation = () => {
    api("GET", "supermix", "/equations", "", "", "").then(res => {
      console.log(res.data);
      this.setState({
        data: res.data.results.equations
      });
    });
  };

  showModal = () => {
    this.setState({
      visible: true
    });
  };

  handleOk = e => {
    console.log(e);
    this.setState({
      visible: false
    });
  };

  handleCancel = e => {
    console.log(e);
    this.setState({
      visible: false
    });
  };

  handleChange = (pagination, filters, sorter) => {
    console.log("Various parameters", pagination, filters, sorter);
    this.setState({
      filteredInfo: filters,
      sortedInfo: sorter
    });
  };

  clearFilters = () => {
    this.setState({ filteredInfo: null });
  };

  clearAll = () => {
    this.setState({
      filteredInfo: null,
      sortedInfo: null
    });
  };

  setAgeSort = () => {
    this.setState({
      sortedInfo: {
        order: "descend",
        columnKey: "age"
      }
    });
  };

  onChange(pageNumber) {
    console.log("Page: ", pageNumber);
  }

  // plant delete API

  onConfirmdelete(id) {
    console.log(id);
    let mesg = "plant delete";

    api("DELETE", "supermix", "/equation", "", "", id).then(res => {
      console.log(res.data);
      this.getallEquation();
      Notification("success", res.data.message);
    });
    console.log(this.state.id);
  }

  render() {
    const columns = [
      {
        title: "Formula",
        dataIndex: "formula",
        key: "formula",
        width: "10%"
      },

      {
        title: "Edit & Delete",
        key: "action",
        width: "7%",
        render: (text, record = this.state.data) => (
          <span>
            <a>
              <Icon
                type="edit"
                style={{ fontSize: "1.2em" }}
                onClick={this.props.passEditEquationRecordtoModal.bind(
                  this,
                  record
                )}
              />
            </a>
            <Divider type="vertical" />
            <a>
              <Popconfirm
                title="Are you sure you want to Delete this?"
                icon={
                  <Icon type="question-circle-o" style={{ color: "red" }} />
                }
                onConfirm={this.onConfirmdelete.bind(this, record.id)}
              >
                <a href="#">
                  <Icon
                    type="delete"
                    style={{ color: "red", fontSize: "1.2em" }}
                  />
                </a>
              </Popconfirm>
            </a>
          </span>
        )
      }
    ];

    return (
      <div style={{ marginTop: "20px" }}>
        <AntTable
          length
          title={() => <EquationTitle reload={this.getallEquation} />}
          className="plantManageTable"
          columns={columns}
          dataSource={this.state.data}
          onChange={this.handleChange}
          pagination={{ defaultPageSize: 7 }}
          size={this.state.size}
        />
      </div>
    );
  }
}

const mapStateToProps = state => null;

const mapDispatchToProps = dispatch => {
  return {
    // if this function dispatches modal will be shown and the data will be drawn :)
    passEditEquationRecordtoModal: record => {
      //this payload is the data we pass into redux which is in the row which we clicked
      dispatch({ type: SWITCH_TO_EDIT_MODE, payload: record });
      console.log(record);
    }
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(ManageEquation);
