import React, { Component } from "react";
import Paragraph from "antd/lib/typography/Paragraph";
import { FlexContainer } from "../../../../Components/styledcomponents/container/FlexGrid";
import AddFiness from "../AddFiness";

const style = {
  fontSize: "medium",
  fontWeight: "600",
  alignSelf: "auto",
  padding: "10px"
};

export default class FinenessTitle extends Component {
  getallplant = () => {
    this.props.reload();
  };
  render() {
    return (
      <FlexContainer titles leveltitles>
        <Paragraph style={style}>Fineness Modulus</Paragraph>
        <AddFiness reload={this.getallplant} />
      </FlexContainer>
    );
  }
}