import React, { Component } from "react";
import { connect } from "react-redux";
import { FlexContainer } from "../../../../styledcomponents/container/FlexGrid";
import TableTileArea from "./TableTileArea";
import AcceptedValue from "./AcceptedValue";
import ManageEquation from "./ManageEquation";
import MaangeTestParamterTitle from "./titles/MaangeTestParamterTitle";
import ManageTestParameter from "./ManageTestParameter";

class TableMaster extends Component {
  renderComponents = () => {
    if (this.props.routepath === "/acceptedvalue") {
      return <AcceptedValue />;
    } else if (this.props.routepath === "/testparameter") {
      return <ManageTestParameter />;
    } else if (this.props.routepath === "/equation") {
      return <ManageEquation />;
    }
  };
  render() {
    return (
      <FlexContainer leveltileareafixed>
        <TableTileArea />
        {this.renderComponents()}
      </FlexContainer>
    );
  }
}

const mapStateToProps = state => {
  return {
    routepath: state.tableReducers.RoutingBetweenTables.routepath
  };
};

const mapDispatchToProps = dispatch => {};

export default connect(mapStateToProps, mapDispatchToProps)(TableMaster);
