import React, { Component } from "react";
import { FlexContainer } from "../../styledcomponents/container/FlexGrid";
import { Input, Select, InputNumber, DatePicker, Table } from "antd";
import { PrimaryButton } from "../../styledcomponents/button/button";
import theme from "../../../theme";
import moment from "moment";
import { Divider, Button, Icon, Popconfirm, Tag } from "antd";
import { api } from "../../services/AxiosService";
import { AntTable } from "../../styledcomponents/table/AntTabl";
import Notification from "../../Constant/Notification";
import history from "../../../Components/Constant/history";

const { Option } = Select;
const error = {
  color: "red",
  fontSize: "12px",
  width: "100px",
  height: "0.2px"
};
const error2 = {
  color: "red",
  fontSize: "12px",
  width: "160px",
  height: "0.2px"
};

export default class SieveTestTrial extends Component {
  constructor(props) {
    super(props);

    this.state = {
      code: "",
      date: "",
      finenessModulus: "",
      status: "NEW",
      userId: "",
      plantCode: "",
      incomingSampleCode: this.props.match.params.sampleId,
      panWeight: "",
      totalWeight: 0,
      weight: [],
      disible: false,
      statusCode: "",
      errormgs: "",
      minvalue: ""
    };
  }

  handleDates(name, dateString, field) {
    const { errors } = this.state;
    console.log(name);
    console.log(dateString);
    console.log(field);
    let convertedDate = moment(dateString).format("YYYY-MM-DD");
    console.log(convertedDate);
    if (name === "date") {
      this.setState({
        date: dateString
      });
    }
  }

  handleChange = (event, field) => {
    this.setState({ [field]: event.target.value });

    event.preventDefault();
    const { name, value } = event.target;
    let errors = this.state.errors;
    console.log(name + " is \n" + value);
    // switch (name) {
    //   case "code":
    //     errors.code = value.length === 0 ? "code can't be empty" : "";
    //     break;
    //   case "status":
    //     errors.status =
    //       value.length === 0
    //         ? "status can't be empty"
    //         : value.length < 2
    //         ? "status \n must be 2 characters long!"
    //         : !isNaN(value)
    //         ? "status won't allow only letters"
    //         : "";
    //     break;

    //   default:
    //     break;
    // }

    this.setState({ errors, [name]: value });
  };
  handleChangeFine = value => {
    const { errors } = this.state;
    console.log(value);
    this.setState({
      finenessModulus: value
    });
  };
  handleChangePan = value => {
    const { errors } = this.state;
    console.log(value);
    this.setState({
      panWeight: value
    });
  };
  handleChangeNumber = value => {
    const { errors } = this.state;
    console.log(value);
    this.setState({
      totalWeight: value
    });
  };

  handleSelect = (name, value) => {
    console.log(name);
    console.log(value);
    // handle select for  plant
    if (name === "plant") {
      this.setState({
        plantCode: value
        // plantEdit: value
      });
    }
    if (name === "user") {
      this.setState({
        userId: value
        // plantEdit: value
      });
    }
  };
  componentDidMount() {
    this.getAllplant();
    this.SelectUser();
    this.getSampleById();
    // this.getalltestTrailByTestId();
  }
  getAllplant() {
    api("GET", "supermix", "/plants", "", "", "").then(res => {
      console.log(res.data.results.plants.length);
      if (res.data.results.plants.length > 0) {
        console.log("ggg");
        let SelectPlants = res.data.results.plants.map((post, index) => {
          return (
            <Option value={post.code} key={index}>
              {post.name}
            </Option>
          );
        });
        this.setState({
          SelectPlants
        });
      }
    });
  }
  SelectUser = () => {
    api("GET", "supermix", "/users", "", "", "").then(res => {
      console.log(res.data);

      if (res.data.results.user.length > 0) {
        let userselect = res.data.results.user.map((post, index) => {
          console.log("kkkkkkkkkk");
          return (
            <Option value={post.id} key={index}>
              {post.userName}
            </Option>
          );
        });
        this.setState({
          userselect
        });
      }
    });
  };

  getSampleById = () => {
    api(
      "GET",
      "supermix",
      "/incoming-sample",
      "",
      "",
      this.state.incomingSampleCode
    ).then(res => {
      console.log(res.data.results.incomingSample);
      let id =
        res.data.results.incomingSample.rawMaterial.materialSubCategory.id;
      console.log(
        res.data.results.incomingSample.rawMaterial.materialSubCategory.id
      );
      this.getSizeBySubId(id);
      // this.createEquationParameters(res.data.results.equationParameters);
      // this.setState({
      //   equationParameters: res.data.results.testparameters
      // });
    });
  };
  getSizeBySubId = id => {
    api(
      "GET",
      "supermix",
      "/sieve-size/material-sub-category",
      "",
      "",
      id
    ).then(res => {
      console.log(res.data);
      // this.createEquationParameters(res.data.results.equationParameters);
      this.setState({
        datalist: res.data.results.materialSubCategoryId
      });
    });
  };
  handleSubmit = e => {
    let eM = "can't be empty";
    e.preventDefault();
    const {
      code,
      date,
      finenessModulus,
      status,
      userId,
      plantCode,
      incomingSampleCode,
      panWeight,
      totalWeight
    } = this.state;

    const data = {
      code: code,
      date: moment(date).format("YYYY-MM-DD"),
      finenessModulus: finenessModulus,
      status: status,
      userId: userId,
      plantCode: plantCode,
      incomingSampleCode: incomingSampleCode,
      panWeight: panWeight,
      totalWeight: totalWeight
    };
    api("POST", "supermix", "/sieve-test", "", data, "").then(res => {
      console.log(res.data);
      Notification("success", res.data.message);
      this.setState({
        statusCode: res.data.status,
        disible: true
      });
    });

    console.log(data);
  };

  addmin = (record, value) => {
    console.log(record);
    console.log(value.target.value);
    this.state.weight[record.id] = value.target.value;
    this.setState({
      errormgs: ""
    });
  };

  handleSubmitTrail = () => {
    let data = [];
    for (var i = 0; i < this.state.datalist.length; i++) {
      data.push({
        cumulativeRetained: this.state.weight[this.state.datalist[i].id],
        sieveTestCode: this.state.code,
        sieveSizeId: this.state.datalist[i].id
      });
    }
    console.log(data);
    if (this.state.weight.length > 0) {
      api("POST", "supermix", "/sieve-test-trial", "", data, "").then(res => {
        console.log(res.data);
        Notification("success", res.data.message);
        this.setState({
          weight: []
        });
        this.getalltestTrailByTestId();
      });
    } else {
      this.setState({
        errormgs: "CumulativeRetained can't be Empty"
      });
    }
  };

  getalltestTrailByTestId = () => {
    let resultData = [];
    api(
      "GET",
      "supermix",
      "/sieve-test-trial/sieve-test",
      "",
      "",
      this.state.code
    ).then(res => {
      let x = res.data.results.sieveTestCode;
      console.log(res.data.results.sieveTestCode);
      for (var i = 0; i < x.length; i++) {
        resultData.push({
          size: x[i].sieveSize.size,
          percentageRetained: x[i].percentageRetained,
          cumulativeRetained: x[i].cumulativeRetained,
          passing: x[i].passing,
          totalWeight: x[i].sieveTest.totalWeight,
          status: x[i].sieveTest.status
        });
      }
      if (resultData.length > 0) {
        this.setState({
          totalWeight: resultData[0].totalWeight,
          status: resultData[0].status
        });
      }

      this.setState({
        resultData
      });
    });
  };

  render() {
    const {
      code,
      date,
      finenessModulus,
      status,
      userId,
      plantCode,
      incomingSampleCode,
      panWeight,
      totalWeight
    } = this.state;
    const Columns = [
      {
        title: "Size",
        dataIndex: "size",
        key: "size",
        width: "50%"
      },

      {
        title: "Retained Weight",
        dataIndex: "cumulativeRetained",
        key: "value",
        render: (text, record = this.state.datalist) => (
          <Input
            id="unit"
            name="unit"
            // value={this.state.minvalue}
            onChange={value => this.addmin(record, value)}
            style={{ width: 80 }}
          />
        )
      }
    ];

    const Columns1 = [
      {
        title: "Sieve Size (mm)",
        dataIndex: "size",
        key: "size",
        width: "15%"
      },
      {
        title: "Cummalative Weight Retained (g)",
        dataIndex: "cumulativeRetained",
        key: "cumulativeRetained"
      },

      {
        title: "Percentage Weight  Retained",
        dataIndex: "percentageRetained",
        key: "percentageRetained"
      },
      {
        title: "Percentage Weight Passing",
        dataIndex: "passing",
        key: "passing"
      }
    ];
    return (
      <FlexContainer column>
        {/* <div className="input_wrapper">
          <h4>Sieve Test</h4>
        </div> */}
        <FlexContainer
          style={{ background: "white", borderRadius: "15px", height: "160px" }}
        >
          <div className="input_wrapper">
            <label htmlFor="status" className="label">
              Code
            </label>
            <Input
              id="code"
              name="code"
              style={{ width: "120px" }}
              value={code}
              placeholder="Enter code"
              onChange={this.handleChange}
              disabled={this.state.disible}
            />
            {/* {errors.code.length > 0 && <div style={error2}>{errors.code}</div>} */}
            <div style={{ height: "22px" }} />
          </div>
          <div className="input_wrapper">
            <label htmlFor="date" className="label">
              Date
            </label>
            <DatePicker
              id="date"
              name="date"
              value={date}
              onChange={(dateString, field) =>
                this.handleDates("date", dateString, field)
              }
              disabled={this.state.disible}
            />
            {/* {errors.date.length > 0 && <div style={error2}>{errors.date}</div>} */}
            <div style={{ height: "22px" }} />
          </div>
          <div className="input_wrapper">
            <label htmlFor="material_state" className="label">
              Plant
            </label>
            <Select
              id="plant"
              name="plant"
              placeholder="Select plant"
              style={{ width: 120 }}
              value={plantCode}
              onChange={value => this.handleSelect("plant", value)}
              disabled={this.state.disible}
            >
              {this.state.SelectPlants}
            </Select>
            {/* {errors.material_state.length > 0 && (
              <div style={error2}>{errors.material_state}</div>
            )} */}
            <div style={{ height: "22px" }} />
          </div>
          <div className="input_wrapper">
            <label htmlFor="material_state" className="label">
              User
            </label>
            <Select
              id="user"
              name="user"
              placeholder="Select user"
              style={{ width: 120 }}
              value={userId}
              onChange={value => this.handleSelect("user", value)}
              disabled={this.state.disible}
            >
              {this.state.userselect}
            </Select>
            {/* {errors.material_state.length > 0 && (
                  <div style={error2}>{errors.material_state}</div>
                )} */}
            <div style={{ height: "22px" }} />
          </div>

          <div className="input_wrapper">
            <label htmlFor="no_of_trial" className="label">
              Weight of Pan
            </label>
            <InputNumber
              id="panWeight"
              name="panWeight"
              style={{ width: 110 }}
              placeholder="Enter panWeight"
              value={panWeight}
              onChange={this.handleChangePan}
              disabled={this.state.disible}
            />
            {/* {errors.no_of_trial.length > 0 && (
              <div style={error}>{errors.no_of_trial}</div>
            )} */}
            <div style={{ height: "22px" }} />
          </div>
          <div className="input_wrapper">
            <label htmlFor="no_of_trial" className="label">
              Fineness Modulus
            </label>
            <InputNumber
              id="finenessModulus"
              name="finenessModulus"
              style={{ width: 110, fontWeight: "bold" }}
              placeholder="Enter finenessModulus"
              value={finenessModulus}
              onChange={this.handleChangeFine}
              disabled={true}
            />
            {/* {errors.no_of_trial.length > 0 && (
              <div style={error}>{errors.no_of_trial}</div>
            )} */}
            <div style={{ height: "22px" }} />
          </div>
          <div className="input_wrapper">
            <label htmlFor="no_of_trial" className="label">
              Total Weight
            </label>
            <InputNumber
              id="totalWeight"
              name="totalWeight"
              style={{ width: 110, fontWeight: "bold" }}
              placeholder="Enter totalWeight"
              value={totalWeight}
              onChange={this.handleChangeNumber}
              disabled={true}
            />
            {/* {errors.no_of_trial.length > 0 && (
              <div style={error}>{errors.no_of_trial}</div>
            )} */}
            <div style={{ height: "22px" }} />
          </div>
          <div className="input_wrapper">
            <label htmlFor="status" className="label">
              Status
            </label>
            <Input
              id="status"
              name="status"
              value={status}
              placeholder="Enter Status"
              style={{ width: 110, fontWeight: "bold" }}
              onChange={this.handleChange}
              disabled={true}
            />
            {/* {errors.status.length > 0 && (
              <div style={error2}>{errors.status}</div>
            )} */}
            <div style={{ height: "22px" }} />
          </div>
          {this.state.disible ? (
            ""
          ) : (
            <div className="input_wrapper">
              <PrimaryButton
                type={"primary"}
                primary
                style={{
                  background: theme.colors.primary,
                  color: theme.colors.white,
                  border: "none",
                  marginRight: "10px"
                }}
                onClick={this.handleSubmit}
              >
                Submit
              </PrimaryButton>
            </div>
          )}
        </FlexContainer>
        <br />
        {this.state.disible && this.state.statusCode === "OK" ? (
          <FlexContainer>
            <FlexContainer>
              <FlexContainer
                style={{
                  background: "#F9F5F5 ",
                  padding: "15px",
                  borderRadius: "15px",
                  width: "650px",
                  height: "550px"
                }}
              >
                <FlexContainer
                  column
                  style={{
                    background: theme.colors.primary,
                    height: "40px",
                    width: "890px",
                    margin: "-15px",
                    borderTopLeftRadius: "15px",
                    borderTopRightRadius: "15px"
                  }}
                >
                  <h3
                    style={{
                      marginTop: "3px",
                      marginLeft: "0px",
                      color: "white"
                    }}
                  >
                    Test Trial
                  </h3>
                </FlexContainer>

                <Table
                  columns={Columns}
                  // title={() => this.state.errormgs}
                  dataSource={this.state.datalist}
                  style={{
                    background: "white",
                    width: "1250px",
                    marginTop: "-10px"
                  }}
                  scroll={{ y: 380 }}
                  size="small"
                  style={{
                    width: "500px",
                    marginTop: "-10px",
                    marginLeft: "80px"
                  }}
                  pagination={false}
                  footer={() => (
                    <div style={{ height: "20px" }}>
                      <PrimaryButton
                        type={"primary"}
                        primary
                        style={{
                          background: theme.colors.primary,
                          color: theme.colors.white,
                          border: "none",
                          marginRight: "20px"
                        }}
                        onClick={this.handleSubmitTrail}
                      >
                        Submit
                      </PrimaryButton>
                    </div>
                  )}
                />
              </FlexContainer>
              &nbsp; &nbsp;
              <FlexContainer
                row
                style={{
                  background: "white",
                  padding: "15px",
                  borderRadius: "15px",
                  width: "650px",
                  height: "auto"
                }}
              >
                <Table
                  columns={Columns1}
                  dataSource={this.state.resultData}
                  style={{
                    background: "white",
                    width: "1250px",
                    marginTop: "-10px"
                  }}
                  scroll={{ y: 280 }}
                  size="small"
                  style={{
                    width: "900px"
                    // marginTop: "10px",
                    // marginLeft: "80px"
                  }}
                  pagination={false}
                  footer={() => (
                    <div
                      style={{
                        height: "20px",
                        display: "flex",
                        flexDirection: "row"
                      }}
                    >
                      <div>
                        Total Weight :<b>{this.state.totalWeight}</b>
                      </div>
                      <div style={{ marginLeft: "200px" }}>
                        <PrimaryButton
                          type={"primary"}
                          primary
                          style={{
                            background: theme.colors.primary,
                            color: theme.colors.white,
                            border: "none",
                            marginRight: "20px"
                          }}
                          onClick={() => history.push(`/testreport`)}
                        >
                          view Results
                        </PrimaryButton>
                      </div>
                    </div>
                  )}
                />
              </FlexContainer>
            </FlexContainer>
          </FlexContainer>
        ) : (
          ""
        )}
      </FlexContainer>
    );
  }
}
