import React, { Component } from "react";

import Paragraph from "antd/lib/typography/Paragraph";
import { FlexContainer } from "../../../../Components/styledcomponents/container/FlexGrid";
import AddAccepetdSize from "../AddAccepetdSize";

const style = {
  fontSize: "medium",
  fontWeight: "600",
  alignSelf: "auto",
  padding: "10px"
};

export default class AddSieveSize extends Component {
  getallplant = () => {
    this.props.reload();
  };
  render() {
    return (
      <FlexContainer titles leveltitles>
        <Paragraph style={style}>Sieve Accepted Value</Paragraph>
        <AddAccepetdSize reload={this.getallplant} />
      </FlexContainer>
    );
  }
}