import React, { Component } from "react";
import { Input, Modal, Button, Icon, Checkbox } from "antd";
import { api } from "../../services/AxiosService";
import { PrimaryButton } from "../../styledcomponents/button/button";
import {
  MasterLevelForm,
  MasterLevelFormTitle
} from "../../styledcomponents/form/MasterLevelForms";
import Notificationfuc from "../../Constant/Notification";
import { connect } from "react-redux";
import { DISABLE_EDIT_MODE } from "../../../redux/action/master/plantlevel/PlantLevel";
import { AntTable } from "../../styledcomponents/table/AntTabl";
import { Main_Category_Name_Aggregate } from "../../Constant/Api";

// function onChange(date, dateString) {
//   console.log(date, dateString);
// }

class AddAcceptedSize extends Component {
  constructor(props) {
    super(props);
    // console.log(this.props.editPlantData)

    this.state = {
      loading: false,
      visible: false,
      errors: {
        material_sub_category_id: ""
      },
      material_sub_category_id: "",
      material_sub_category_name: "",
      size: "",
      errormgs: "",
      type: "add",
      datalist: "",
      min: [],
      max: [],
      minvalue: "",
      maxvalue: ""
    };
  }

  onChangeCheck = checkedValues => {
    console.log("checked = ", checkedValues);
    this.setState({
      material_sub_category_id: checkedValues[0]
    });
    this.getAllSizeBySubId(checkedValues[0]);
  };
  getAllSizeBySubId = id => {
    api(
      "GET",
      "supermix",
      "/sieve-size/sieve-accepted-value/",
      "",
      "",
      id
    ).then(res => {
      console.log(res.data.results.materialSubCategoryId);
      this.setState({
        datalist: res.data.results.materialSubCategoryId
      });
    });
  };
  componentDidMount() {
    this.getSubCategoryByMain();
  }

  reloadPage() {
    window.location.reload();
  }
  getSubCategoryByMain = () => {
    let optData = [];
    api(
      "GET",
      "supermix",
      "/material-sub-category/materialCategoryName",
      "",
      "",
      Main_Category_Name_Aggregate
    ).then(res => {
      let dataSub = res.data.results.materialSubCategories;
      console.log(dataSub);
      for (var i = 0; i < dataSub.length; i++) {
        optData.push({
          label: dataSub[i].name,
          value: dataSub[i].id
        });
      }
      console.log(optData);
      this.setState({
        optData
      });
    });
  };

  handleChange = (event, field) => {
    this.setState({ [field]: event.target.value });
    this.setState({ errormgs: "" });
    event.preventDefault();
    const { name, value } = event.target;
    let errors = this.state.errors;
    console.log(name + " is \t" + value);
    switch (name) {
      case "size":
        errors.size = value.length === 0 ? "Code can't be empty" : "";
        break;
      default:
        break;
    }

    this.setState({ errors, [name]: value });
  };

  handleSubmit = event => {
    event.preventDefault();
    if (this.state.material_sub_category_id.length === 0) {
      this.setState({
        errors: {
          material_sub_category_id: "Sub category  can't be empty"
        }
      });
    } else if (
      this.state.material_sub_category_id.length === 0 &&
      this.state.errors.material_sub_category_id.length === 0
    ) {
      this.setState({
        errors: {
          material_sub_category_name:
            this.state.material_sub_category_id ||
            "Sub category  can't be empty"
        }
      });
    } else if (this.state.errors.material_sub_category_id.length === 0) {
      console.log(this.state.errors);

      let data = [];
      for (var i = 0; i < this.state.datalist.length; i++) {
        data.push({
          sieveSizeId: this.state.datalist[i].id,
          materialSubCategoryId: this.state.material_sub_category_id,
          min: this.state.min[this.state.datalist[i].id],
          max: this.state.max[this.state.datalist[i].id]
        });
      }

      if (this.state.type === "add") {
        api("POST", "supermix", "/sieve-accepted-value", "", data, "")
          .then(
            res => {
              console.log(res.data);

              Notificationfuc("success", res.data.message);
              this.props.reload();
              this.reloadPage();
              // this.setState({ loading: true });
              this.setState({
                material_sub_category_id: "",
                size: "",
                min: [],
                max: [],
                minvalue: "",
                maxvalue: "",
                errormgs: "",
                visible: false
              });
            },
            error => {
              // this.setState({
              //   errormgs: error.validationFailures[0]
              // });
              console.log("DEBUG34: ", error);
              // console.log(HandelError(error.validationFailures[0]));
            }
          )
          .catch(error => {
            this.setState({
              // errormgs: "Plant Name Exist"
            });
            console.log(error);
          });
      }
      // else {

      // }
      console.log(data);
      console.log("form is valid");
    }
  };

  showModal = () => {
    this.setState({
      visible: true,
      size: "",
      material_sub_category_id: ""
    });
  };

  handleCancel = () => {
    if (this.state.type === "edit") {
      // we call the redux function to dispatch and delete all the global redux state to close the modal
      this.props.setPlantVisiblity();
    }

    this.setState({
      visible: false,
      formValid: false,
      errors: {
        material_sub_category_id: ""
      },
      material_sub_category_id: "",
      size: "",
      min: [],
      max: [],
      minvalue: "",
      maxvalue: ""
    });
    this.reloadPage();
  };

  componentWillReceiveProps(nextProps) {
    // this.setState({
    //   visible: nextProps.visible,
    //   plant_code: nextProps.editPlantData.code,
    //   plant_name: nextProps.editPlantData.name,
    //   plant_address: nextProps.editPlantData.address,
    //   plant_contactno: nextProps.editPlantData.phoneNumber,
    //   type: nextProps.type
    // });
  }

  addmin = (record, value) => {
    console.log(record);
    console.log(value.target.value);
    this.state.min[record.id] = value.target.value;
  };
  addmax = (record, value) => {
    this.state.max[record.id] = value.target.value;
  };
  render() {
    console.log(this.state.min);
    console.log(this.state.min[85]);
    const { visible, loading } = this.state;
    const Columns = [
      {
        title: "Size",
        dataIndex: "size",
        key: "size"
      },
      {
        title: "Max Value",
        dataIndex: "max_value",
        key: "max_value",
        render: (text, record = this.state.datalist) => (
          <Input
            id="unit"
            name="unit"
            // value={this.state.maxvalue}
            onChange={value => this.addmax(record, value)}
            style={{ width: 100 }}
          />
        )
      },
      {
        title: "Min Value",
        dataIndex: "min_value",
        key: "min_value",
        render: (text, record = this.state.datalist) => (
          <Input
            id="unit"
            name="unit"
            // value={this.state.minvalue}
            onChange={value => this.addmin(record, value)}
            style={{ width: 100 }}
          />
        )
      }
    ];
    return (
      <div>
        <PrimaryButton
          onClick={this.showModal}
          style={{
            background: "#001328",
            color: "white",
            border: "none",
            width: "190px",
            marginLeft: "-10px"
          }}
        >
          Add Sieve Accepted Value
        </PrimaryButton>
        <Modal
          visible={visible}
          closable={false}
          loading={loading}
          onCancel={this.handleCancel}
          title={
            <MasterLevelFormTitle>
              <p
                style={{
                  color: "white"
                }}
              >
                {this.state.type === "edit"
                  ? "Edit Sieve Accepted Values"
                  : "Add Sieve Accepted Values"}
              </p>
              <Icon
                type="close-circle"
                onClick={this.handleCancel}
                style={{
                  color: "white"
                }}
              />
            </MasterLevelFormTitle>
          }
          footer={[
            <Button key="back" onClick={this.handleCancel}>
              Cancel
            </Button>,
            <PrimaryButton
              key="submit"
              loading={loading}
              onClick={this.handleSubmit}
              style={{ background: "#001328", color: "white", border: "none" }}
            >
              {this.state.type === "edit" ? "Edit" : "Save"}
            </PrimaryButton>
          ]}
          width="600px"
        >
          <MasterLevelForm style={{ justifyContent: "space-evenly" }}>
            <div className="input_wrapper">
              <label for="sub_category" className="label">
                Material Sub Category:
              </label>
              <Checkbox.Group
                options={this.state.optData}
                onChange={this.onChangeCheck}
              />
              <div style={{ height: "12px" }}></div>
            </div>
            <div className="input_wrapper">
              <AntTable
                dataSource={this.state.datalist}
                // size={this.state.size}
                bordered={false}
                columns={Columns}
                showHeader={true}
                pagination={false}
                scroll={{ y: 200 }}
                style={{
                  height: "auto",
                  width: "550px",
                  backgroundClip: "padding-box",
                  cellSpacing: "0"
                }}
              />
            </div>
          </MasterLevelForm>
        </Modal>
      </div>
    );
  }
}

const mapStateToProps = state => {
  //getting the global redux state to get the data from the EditPlantReducer.js
  return {
    visible: state.plantLevelReducers.EditPlantReducer.visible,
    type: state.plantLevelReducers.EditPlantReducer.type,
    editPlantData: state.plantLevelReducers.EditPlantReducer.editPlantData
  };
};

const mapDispatchToProps = dispatch => {
  return {
    // setting visible to false if we close the modal .. and all state data will be deleted if this function is dispatched
    setPlantVisiblity: () => {
      dispatch({ type: DISABLE_EDIT_MODE });
      console.log("edit modal closed");
    }
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(AddAcceptedSize);
